function getRandomInt (min, max) {//Math.random()0-111
    return Math.floor(Math.random() * (max - min + 1) + min)
}



export function shuffle (arr) {//0iarr[i]
    let _arr = arr.slice() //shuffle
    for(let i = 0; i < _arr.length; i++){
        let j = getRandomInt(0, i)
        let t = _arr[i]
        _arr[i] = _arr[j]
        _arr[j] = t
    }
    return _arr
}


